.filtered {
  width: 300px;
  margin: 20px auto;
}

.listing {
  text-align: left;
  width: 100px;
  font-size: 0.8em;
}

@media screen and (min-width: 780px) {
  .filtered {
    width: 600px;
    margin: 20px auto;
    text-align: center;
  }
  .listing {
    text-align: left;
    width: 200px;
    font-size: 1em;
  }
}

.add_flex {
  display: -ms-flexbox;
  display: flex;
}

.align_center {
  text-align:center;
}

.alert {
    padding: 15px;
    margin-bottom: 20px;
    border: 1px solid transparent;
    border-radius: 4px;
}

.alert-info {
  color: #31708f;
  background-color: #d9edf7;
  border-color: #bce8f1;
}



